html, body {
    width: 100%;
    height: auto;
}

body.with-bg {
    background-image: url('/public/assets/img/login-wallpaper.jpg');
    background-position-y: 20%;
    background-repeat: no-repeat;
}

.login-form {
    width: 340px;
    margin: 50px auto;
  	font-size: 15px;
}
.login-form form {
    margin-bottom: 15px;
    background: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 30px;
}
.login-form h2 {
    margin: 0 0 15px;
}
.form-control, .btn {
    min-height: 38px;
    border-radius: 2px;
}
.btn {        
    font-size: 15px;
    font-weight: bold;
}
.form-group {
    margin-bottom: 1rem;
}